import React, { Component } from "react";
import { onActivePeer } from "./components/Peer/Peer";
import { generateRandomName } from "./components/HelperFunctions/HelperFunctions";
import "./General.css";
import LeftMenu from "./components/LeftMenu/LeftMenu.js";

class App extends Component {
	constructor(props) {
		super(props);

		this.state = {
			selfId: "?",
			showId: "?",
			selfName: generateRandomName(),
		};
	}

	componentDidMount() {
		onActivePeer((selfId) => {
			this.setState({
				selfId: selfId,
				showId: selfId,
			});
		});
	}

	render() {
		return (
			<div className="App">
				<LeftMenu
					showId={this.state.showId}
					selfName={this.state.selfName}
				></LeftMenu>
			</div>
		);
	}
}

export default App;
